              <text class="info-text">{{ experience.date }} {{ experience.time }}</text>
            </view>
            <view class="info-item">
              <text class="info-icon">üìç</text>
              <text class="info-text">{{ experience.location }}</text>
            </view>
            <view class="info-item">
              <text class="info-icon">üë•</text>
              <text class="info-text" :class="{ 'sold-out': experience.availableSlots === 0 }">
                {{ experience.availableSlots === 0 ? 'Â∑≤Êª°' : `Ââ©‰Ωô ${experience.availableSlots} ‰∏™ÂêçÈ¢ù` }}
              </text>
            </view>
          </view>
          <button 
            class="book-btn"
            :class="{ disabled: experience.availableSlots === 0 }"
            :disabled="experience.availableSlots === 0"
            @click="openBookingForm(experience)"
          >
            {{ experience.availableSlots === 0 ? 'Â∑≤Êª°' : 'Á´ãÂç≥È¢ÑÁ∫¶' }}
          </button>
        </view>
      </view>
    </view>

    <!-- Á©∫Áä∂ÊÄÅ -->
    <view class="empty-state" v-else>
      <text class="empty-icon">üë®‚Äçüç≥</text>
      <text class="empty-text">ÊöÇÊó†ÁæéÈ£üÂà∂‰Ωú‰ΩìÈ™åÊ¥ªÂä®</text>
    </view>

    <!-- È¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó -->
    <view class="booking-modal" v-if="showBookingForm" @click.self="closeBookingForm">
      <view class="modal-content">
        <view class="modal-header">
          <text class="modal-title">È¢ÑÁ∫¶‰ø°ÊÅØ</text>
          <text class="close-btn" @click="closeBookingForm">√ó</text>
        </view>
        
        <!-- ÈÄâ‰∏≠ÁöÑ‰ΩìÈ™åÊ¥ªÂä®‰ø°ÊÅØ -->
        <view class="selected-experience-info" v-if="selectedExperience">
          <text class="experience-name">{{ selectedExperience.title }}</text>
          <text class="experience-datetime">{{ selectedExperience.date }} {{ selectedExperience.time }}</text>
          <text class="experience-place">{{ selectedExperience.location }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂßìÂêç <text class="required">*</text></text>
          <input 
            class="form-input"
            :class="{ error: formErrors.name }"
            v-model="formData.name"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂßìÂêç"
          />
          <text class="error-text" v-if="formErrors.name">{{ formErrors.name }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÊâãÊú∫Âè∑ <text class="required">*</text></text>
          <input 
            class="form-input"
            :class="{ error: formErrors.phone }"
            v-model="formData.phone"
            type="number"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊâãÊú∫Âè∑"
          />
          <text class="error-text" v-if="formErrors.phone">{{ formErrors.phone }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂèÇ‰∏é‰∫∫Êï∞</text>
          <input 
            class="form-input"
            v-model="formData.participants"
            type="number"
            placeholder="ÈªòËÆ§1‰∫∫"
          />
        </view>
        
        <view class="form-group">
          <text class="form-label">Â§áÊ≥®</text>
          <textarea 
            class="form-textarea"
            v-model="formData.remark"
            placeholder="Â¶ÇÊúâÁâπÊÆäÈúÄÊ±ÇËØ∑Â§áÊ≥®ÔºàÂ¶ÇÈ£üÁâ©ËøáÊïèÁ≠âÔºâ"
          ></textarea>
        </view>
        
        <button class="submit-btn" @click="submitBooking">Á°ÆËÆ§È¢ÑÁ∫¶</button>
      </view>
    </view>

    <!-- È¢ÑÁ∫¶ÊàêÂäüÁ°ÆËÆ§ÂºπÁ™ó -->
    <view class="booking-modal" v-if="showConfirmation" @click.self="closeConfirmation">
      <view class="modal-content confirmation-modal">
        <view class="success-icon">‚úì</view>
        <text class="success-title">È¢ÑÁ∫¶ÊàêÂäü</text>
        <view class="confirmation-details">
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®ÂêçÁß∞</text>
            <text class="detail-value">{{ confirmationData.experienceTitle }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®Êó∂Èó¥</text>
            <text class="detail-value">{{ confirmationData.experienceDate }} {{ confirmationData.experienceTime }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®Âú∞ÁÇπ</text>
            <text class="detail-value">{{ confirmationData.experienceLocation }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">È¢ÑÁ∫¶‰∫∫</text>
            <text class="detail-value">{{ confirmationData.name }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">ËÅîÁ≥ªÁîµËØù</text>
            <text class="detail-value">{{ confirmationData.phone }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">ÂèÇ‰∏é‰∫∫Êï∞</text>
            <text class="detail-value">{{ confirmationData.participants }}‰∫∫</text>
          </view>
        </view>
        <view class="confirmation-actions">
          <button class="view-records-btn" @click="goToRecordsFromConfirm">Êü•ÁúãÊàëÁöÑÈ¢ÑÁ∫¶</button>
          <button class="close-confirm-btn" @click="closeConfirmation">ËøîÂõûÊ¥ªÂä®ÂàóË°®</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import CustomNavBar from '@/components/CustomNavBar.vue'
import FoodBookingService from '@/services/food-booking.service.js'

export default {
  components: { CustomNavBar },
  data() {
    return {
      navPaddingTop: 100,
      experiences: [],
      selectedExperience: null,
      showBookingForm: false,
      showConfirmation: false,
      loading: true,
      confirmationData: {
        experienceTitle: '',
        experienceDate: '',
        experienceTime: '',
        experienceLocation: '',
        name: '',
        phone: '',
        participants: 1
      },
      formData: {
        name: '',
        phone: '',
        participants: 1,
        remark: ''
      },
      formErrors: {}
    }
  },
  onLoad() {
    const info = uni.getSystemInfoSync()
    this.navPaddingTop = (info.statusBarHeight || 20) + 75
    this.loadExperiences()
  },
  methods: {
    /**
     * Âä†ËΩΩÁæéÈ£üÂà∂‰Ωú‰ΩìÈ™åÊ¥ªÂä®ÂàóË°®
     */
    async loadExperiences() {
      this.loading = true
      try {
        this.experiences = await FoodBookingService.getCookingExperiences()
      } catch (err) {
        console.error('Failed to load experiences:', err)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },
    
    /**
     * Ëé∑ÂèñÈöæÂ∫¶Á≠âÁ∫ßÊ†∑ÂºèÁ±ª
     * @param {string} difficulty - ÈöæÂ∫¶Á≠âÁ∫ß
     * @returns {string}
     */
    getDifficultyClass(difficulty) {
      const classMap = {
        'ÁÆÄÂçï': 'easy',
        '‰∏≠Á≠â': 'medium',
        'Âõ∞Èöæ': 'hard'
      }
      return classMap[difficulty] || 'medium'
    },
    
    /**
     * Ë∑≥ËΩ¨Âà∞È¢ÑÁ∫¶ËÆ∞ÂΩïÈ°µÈù¢
     */
    goToRecords() {
      uni.navigateTo({
        url: '/pages/food-healing/booking-records'
      })
    },
    
    /**
     * ÊâìÂºÄÈ¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó
     * @param {Object} experience - ÁæéÈ£üÂà∂‰Ωú‰ΩìÈ™åÊ¥ªÂä®ÂØπË±°
     */
    openBookingForm(experience) {
      this.selectedExperience = experience
      this.showBookingForm = true
      this.resetForm()
    },
    
    /**
     * ÂÖ≥Èó≠È¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó
     */
    closeBookingForm() {
      this.showBookingForm = false
      this.selectedExperience = null
      this.resetForm()
    },
    
    /**
     * ÈáçÁΩÆË°®ÂçïÊï∞ÊçÆ
     */
    resetForm() {
      this.formData = {
        name: '',
        phone: '',
        participants: 1,
        remark: ''
      }
      this.formErrors = {}
    },

    /**
     * È™åËØÅË°®ÂçïÊï∞ÊçÆ
     * @returns {boolean}
     */
    validateForm() {
      const result = FoodBookingService.validateFoodBookingForm(this.formData)
      
      // ËΩ¨Êç¢ÈîôËØØÊ†ºÂºè
      const errors = {}
      if (!result.valid) {
        result.errors.forEach(err => {
          errors[err.field] = err.message
        })
      }
      
      this.formErrors = errors
      return result.valid
    },
    
    /**
     * ÂÖ≥Èó≠Á°ÆËÆ§ÂºπÁ™ó
     */
    closeConfirmation() {
      this.showConfirmation = false
      this.confirmationData = {
        experienceTitle: '',
        experienceDate: '',
        experienceTime: '',
        experienceLocation: '',
        name: '',
        phone: '',
        participants: 1
      }
    },
    
    /**
     * ‰ªéÁ°ÆËÆ§ÂºπÁ™óË∑≥ËΩ¨Âà∞È¢ÑÁ∫¶ËÆ∞ÂΩï
     */
    goToRecordsFromConfirm() {
      this.closeConfirmation()
      this.goToRecords()
    },
    
    /**
     * Êèê‰∫§È¢ÑÁ∫¶
     */
    async submitBooking() {
      if (!this.validateForm()) {
        return
      }
      
      if (!this.selectedExperience) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Ê¥ªÂä®',
          icon: 'none'
        })
        return
      }
      
      const result = await FoodBookingService.createFoodBooking(
        this.selectedExperience.id,
        this.selectedExperience,
        this.formData
      )
      
      if (result.success) {
        // Êõ¥Êñ∞Êú¨Âú∞Ê¥ªÂä®Ââ©‰ΩôÂêçÈ¢ù
        const expIndex = this.experiences.findIndex(e => e.id === this.selectedExperience.id)
        if (expIndex !== -1) {
          this.experiences[expIndex].availableSlots -= (this.formData.participants || 1)
        }
        
        // ‰øùÂ≠òÁ°ÆËÆ§‰ø°ÊÅØ
        this.confirmationData = {
          experienceTitle: this.selectedExperience.title,
          experienceDate: this.selectedExperience.date,
          experienceTime: this.selectedExperience.time,
          experienceLocation: this.selectedExperience.location,
          name: this.formData.name,
          phone: this.formData.phone,
          participants: this.formData.participants || 1
        }
        
        // ÂÖ≥Èó≠Ë°®ÂçïÔºåÊòæÁ§∫Á°ÆËÆ§ÂºπÁ™ó
        this.showBookingForm = false
        this.showConfirmation = true
        this.selectedExperience = null
        this.resetForm()
      } else {
        uni.showToast({
          title: result.message || 'È¢ÑÁ∫¶Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      }
    }
  }
}
</script>

